<template>
  <div>
    <v-header>
    </v-header>

    <v-content :type="'none'">
      <div class="section">
        <div class="matter">
          <div class="title">开户名</div>
          <div class="edit">{{realName}}</div>
        </div>
      </div>
      <div class="section">
        <div class="matter">
          <div class="title">开户银行</div>
          <div class="edit" @click="openCardBox">
            <p class="text color-9" v-if="!card.name">请选择开户银行</p>
            <p class="text" v-else>{{card.name}}</p>
            <span class="img transfer"></span>
          </div>
        </div>
        <div class="matter">
          <div class="title">银行卡号</div>
          <div class="edit" style="width:75%;">
            <input class="text" placeholder="请输入银行卡号" v-model="cardNum"
                   type="tel" style="width:100%;" maxlength="19"/>
          </div>
        </div>
      </div>
      <v-inner-btn>
        <input :class="['input',card && cardNum.length>=16 &&  cardNum.length<=19 ? 'ok':'nk']"
               value="确认添加" type="button" @click="toAdd"/>
      </v-inner-btn>
    </v-content>

    <div :class="['card-box','tran-all-3','z-index-max',cardOpen ? 'active' : '']">
      <div class="warp">
        <div class="title">请选择开户行</div>
        <ul class="cardList">
          <li v-for="item in bankList" :class="item.id == card.id ? 'color-main':''" @click="clickCard(item)">
            <span class="img bank"></span>
            <p class="card-info">{{item.name}} <span class="limit">单笔限额{{item.limit}}元</span></p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  import VHeader from '../../components/Header'
  import VContent from '../../components/Content'
  import VInnerBtn from '../../components/InnerBtn'
  import {toast} from '../../assets/js/toast'
  import {lt, url} from '../../const'
  import $ from 'jquery'
  import {DJUtils} from '../../assets/js/common'

  export default{
    route: {
      data({to:{query}}){
        if (!query.realName || query.realName == 'null') {
          window.location.href = "#/wallet/bankCard";
          return;
        }
        return {
          realName: query.realName,
          card: {},
          cardNum: '',
          cardOpen: false,
          type: (query.type ? query.type : 0)
        }
      }
    },
    ready(){
      $('.none_bg').css('height', $(window).height() * 0.9 + "px");
      this.$http.get(url.getBankList).then(
              function ({data:{success, data, resultMsg}}) {
                if (success) {
                  this.bankList = data;
                } else {
                  toast.alert(resultMsg);
                }
              }
      );
    },
    data(){
      return {
        realName: '',
        bankList: [],
        card: {},
        cardNum: '',
        cardOpen: false,
        type: 0
      }
    },
    methods: {
      openCardBox(){
        let vm = this;
        vm.cardOpen = true;
        toast.mask(function () {
          vm.cardOpen = false;
          toast.hide_mask();
        }, true);
      },
      clickCard(card){
        this.card = card;
        this.cardOpen = false;
        toast.hide_mask();
      },
      toAdd(){
        let card = this.card;
        if (card.name && this.cardNum.length >= 16 && this.cardNum.length <= 19) {
          this.$http.get(url.addBankCard, {code: card.code, name: card.name, bankCard: this.cardNum}).then(
                  function ({data:{success, data, resultMsg}}) {
                    if (success) {
                      toast.alert('新增银行卡成功');
                      if (this.type == 1) {
                        window.location.href = "#/wallet/recharge";
                      } else if (this.type == 2) {
                        window.location.href = "#/wallet/cash";
                      } else {
                        window.location.href = "#/wallet/bankCard";
                      }
                    } else {
                      toast.alert(resultMsg);
                    }
                  }
          );
        }
      }
    },
    events: {
      'backtip': function () {
        if (this.type > 0) {
          window.location.href = "#/wallet/home";
        } else {
          DJUtils.backHistory();
        }
      }
    },
    components: {VHeader, VContent, VInnerBtn}
  }
</script>
<style scoped>
  .card-box {
    visibility: hidden;
    height: 0;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .card-box.active {
    height: 4rem;
    visibility: visible;
  }

  .card-box .warp {
    height: 100%;
    background-color: #fff;
  }

  .card-box .title {
    text-align: center;
    font-size: .16rem;
    color: #333;
    height: .45rem;
    line-height: .45rem;
    border-bottom: .01rem solid #e5e5e5;
  }

  .cardList {
    overflow: scroll;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    height: 3.55rem;
  }

  .cardList li {
    height: .55rem;
    line-height: .55rem;
    text-align: left;
    border-bottom: .01rem solid #e5e5e5;
    position: relative;
  }

  .img.bank {
    position: absolute;
    width: .25rem;
    height: .25rem;
    background-size: .25rem;
    background-image: url(../../assets/img/wallet/bank.png);
    left: .15rem;
    top: .15rem;
  }

  .cardList .card-info {
    padding-left: .55rem;
  }

  .card-info .limit {
    padding-left: .1rem;
    font-size: .12rem;
    color: #999;
  }
</style>




